.container {
  width: 100%;
  background-color: #f3f4f6;
  height: 82.5vh;
  overflow-y: hidden;

  .buttonText {
    background: #242226;
    border: 1px solid transparent;
    cursor: pointer;
    padding: 10px 20px;
    margin-top: -5px;
    color: #ffffff;

    &:hover {
      background-color: #55555e;
    }
  }
}